import styles from "./skills.module.css";
import listening from '../../img/skills-listening.svg';
import reading from '../../img/skills-reading.svg';
import speaking from '../../img/skills-speaking.svg';
import writing from '../../img/skills-writing.svg';

const SkillCard = ({ image, title, description, extraText }) => {
  return (
    <div className={styles.card}>
      <img className={styles.cardImage} src={image} alt={title} />
      <div className={styles.cardContent}>
        <h3>{title}</h3>
        <p className={styles.desc}>{description}</p>
        {extraText && <p>{extraText}</p>}
        <button className={styles.button}>Learn more</button>
      </div>
    </div>
  );
};

const Skills = () => {
  return (
    <section className={styles.container}>
      <h2 className={styles.title}>Skills</h2>
      <div className={styles.skillsGrid}>
        {/* Speaking Card - выделенный */}
        <SkillCard 
          image={speaking} 
          title="Speaking" 
          description="Improve your English skills and confidence. Live classes and interactive lessons online. 20% extra free for a limited time only."
          extraText="Learn English online and improve your skills through our high-quality courses and resources – all designed for adult language learners."
        />

        <div className={styles.threeCard}>
          <div className={styles.secondBlock}>
            {/* Writing Card */}
            <SkillCard 
              image={writing} 
              title="Writing" 
              description="One of the most important and extensive areas of natural science, the science that studies substances, also their composition."
            />
            {/* Reading Card */}
            <SkillCard 
              image={reading} 
              title="Reading" 
              description="Perception and response actions of the user resulting from the use and/or upcoming use of the product, system, or service."
            />
          </div>
          {/* Listening Card - широкий */}
          <div className={styles.cardLast}>
            <div className={styles.lastCardDesc}>
              <h3>Listening</h3>
              <p>Here you can find activities to practice your listening skills. Listening will help you to improve your understanding.</p>
              <button className={styles.button}>Learn more</button>
            </div>
            <img className={styles.cardImage} src={listening} alt="Listening" />
          </div>
        </div>
      </div>
    </section>
  );
};

export default Skills;
